/* Side Navigation */
@media (min-width: 225px) {
  .side-nav {
    width: 10%;
    border: none;
    border-radius: 0;
    overflow-y: auto;
    background-color: #111;
    color: #eee;
    opacity: 0.5;
    float: left;
    margin-top: -55px;
  }
  .side-nav > li > a {
    width: 225px;
  }
  .side-nav li a:hover,
  .side-nav li a:focus {
    outline: none;
    background-color: #000 !important;
    color: #fff !important;
  }
}

/*.side-nav>li>ul {
	padding: 0;
}*/
.side-nav > li > a {
  display: block;
  padding: 20px 30px;
  text-decoration: none;
  color: #999 !important;
}
